:root {
  --darkest-color: #0a0908;
}

.App {
  text-align: center;
  font-family: Manrope;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: ManropeBold;
}

.App-header {
  background-color: #22333b;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@font-face {
  font-family: "ManropeBold";
  src: local("ManropeBold"), url(./fonts/Manrope-ExtraBold.woff2) format("woff2");
}

@font-face {
  font-family: "Manrope";
  src: local("Manrop"), url(./fonts/Manrope-Regular.woff2) format("woff2");
}

.TitleBContainer {
  text-align: right;
}

.TitleB {
  font-size: clamp(25px, 2rem, 4rem);
  margin-right: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.navMenuList {
  font-size: 1.3rem;
  display: flex;
  text-align: left;
  flex-direction: column;
  justify-content: left;
  height: 100%;
  padding: 0px;
  margin: 0px;
}

.navMenuList ul {
  padding-left: 10px;
}

.navMenuList li {
  cursor: pointer;
  transition: all 0.1s ease-in-out;
  max-width: 100%;
}

.navMenuList li:hover {
  cursor: pointer;
  transform: translateX(5px);
  transform: scale(0.998);
  color: #c6ac8f;
}

.mainScreen {
  background-color: #5e503f;
  border-color: #5e503f;
  border-radius: 30px;
  border-width: 15px;
  border-style: solid;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: clamp(200px, 80vh, 1000px);
  width: clamp(320px, 90vw, 1000px);
  margin: auto;
}

.projName {
  font-family: ManropeBold;
  font-size: clamp(2rem, 2.6rem, 4rem);
  margin: 0px 5px;
  white-space: normal;
  transition: all 0.2s ease-in-out;
}

projName:after {
  content: " - ";
}

@media screen and (max-width: 600px) {
  .projName {
    font-size: clamp(10px, 1.5rem, 2rem);
  }
}

.projName:hover {
  cursor: pointer;
  transform: translateY(10px);
  color: #eae0d5;
}

.projsZone {
  background-color: #5e503f;
  border-radius: 20px;
  padding-top: 20px;
  text-align: center;
  width: clamp(50%, 90%, 100%);
  margin: auto;
}

.projSel {
  color: #eae0d5;
  text-decoration: underline;
}

.projectTile {
  background-color: #eae0d5;
  border-radius: 20px;
  color: var(--darkest-color);
  align-items: center;
  font-size: clamp(10px, 1.5em, 1rem);
  text-align: left;
  padding: 10px 20px;
  height: clamp(50%, 80%, 90%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
}

.projectTile h1 {
  font-size: clamp(2rem, 3rem, 6rem);
  margin: 5px;
}

.projectHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-bottom: 2.5px solid #5e503f;
}

@media screen and (max-width: 600px) {
  .projectHeader {
    flex-direction: column;
    align-items: flex-start;
  }
}

.projectDescription {
  width: 100%;
  display: grid;
  --auto-grid-min-size: 16rem;
  grid-template-columns: repeat(auto-fill, minmax(var(--auto-grid-min-size), 1fr));
  grid-gap: 1rem;
  max-width: 760px;
  margin: auto;
  font-size: clamp(8px, 1rem, 2rem);
}

.projectDescription p {
  padding: 5px;
}

.projLink {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: right;
  padding: 2rem 3rem;
  background-color: #C6AC8F;
  border-radius: 100px;
  margin: auto;
  text-decoration: none;
  color: white;
}
