input[type="range"] {
  -webkit-appearance: none;
  display: block;
  margin: 0;
  width: 100%;
  background: transparent;
}
input[type="range"]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  color: transparent;
  background: #c6ac8f;
  border-radius: 999px;
  border: none;
}
input[type="range"]::-moz-range-track {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  color: transparent;
  background: #c6ac8f;
  border-radius: 999px;
  border: none;
}
input[type="range"]::-ms-track {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  color: transparent;
  background: #c6ac8f;
  border-radius: 999px;
  border: none;
}
input[type="range"]::-ms-fill-lower {
  display: none;
}
input[type="range"]::-ms-fill-upper {
  display: none;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 25px;
  width: 25px;
  border-radius: 30px;
  background: #22333b;
}
input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 25px;
  width: 25px;
  border-radius: 30px;
  background: #22333b;
}
input[type="range"]::-ms-thumb {
  -webkit-appearance: none;
  height: 25px;
  width: 25px;
  border-radius: 30px;
  background: #22333b;
}
.ticks {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
}
.tick {
  font-size: 1rem;
  position: relative;
  display: flex;
  justify-content: center;
  width: 1px;
  background: gray;
  height: 10px;
  margin-bottom: 20px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
